
import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Brand, Goods } from 'src/app/class';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BrandService } from 'src/app/server/brand.service';
import { SharedService } from 'src/app/server/shared.service';

@Component({
  selector: 'app-brand-detail',
  templateUrl: './brand-detail.component.html',
  styleUrls: ['./brand-detail.component.scss']
})
export class BrandDetailComponent implements OnInit {

  b:Brand;
  brandForm: FormGroup;
  constructor(
    private route: ActivatedRoute,
    private service: BrandService,
    private location: Location,
    private _formBuilder: FormBuilder,
    private share:SharedService,
  ) { }

  ngOnInit(): void {
    const name = this.route.snapshot.paramMap.get('name');
    this.service.findBrandByName(name).subscribe((data)=>{
      this.b=data;
    })
    this.brandForm = this._formBuilder.group({
      name: [this.b.name],
      url: [this.b.url, Validators.required],
    });
  }

  save() {
    alert(this.brandForm.get("dateTime").value)
  }
  submit(): void {
    this.add(this.brandForm.get('name').value, this.brandForm.get('url').value)
  }
  add(name: string, url: string) {
    this.service.updateBrand({ name,url } as Brand).subscribe((data)=>{
      if(data==true){
        this.share.openSnackBar('','')
        this.goBack()
      }
      else{
        this.share.openSnackBar('','')
      }
        
    })
  }

  delete(name:string){
    if(confirm("?")){
      this.service.deleteBrand(name).subscribe((data)=>{
        if(data==true){
          this.share.openSnackBar('','')
          this.goBack()
        }
        else{
          this.share.openSnackBar('','')
        }
      })
    }

  }

  goBack(): void {
    this.location.back();
  }

}
