import { Brand, Client} from '../../class';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { ClientService } from 'src/app/server/client.service';


@Component({
  selector: 'app-client-list',
  templateUrl: './client-list.component.html',
  styleUrls: ['./client-list.component.scss']
})
export class ClientListComponent implements OnInit {

  allClient:Client[]=[]
  dataSource:MatTableDataSource<Client>
  displayedColumns: string[]=['','','','']

  private searchTerms = new Subject<string>();
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  constructor(
    private service:ClientService
  ) { }
  
  ngOnInit(): void {
    this.service.getAllClient().subscribe((data)=>{
      this.allClient=data;
      this.dataSource=new MatTableDataSource<Client>(this.allClient);
      this.dataSource.paginator = this.paginator;
    })
    this.searchTerms
      .pipe(
        // debounceTime(300)  300 
        debounceTime(300),
        // 
        distinctUntilChanged(),
        //  debounce  distinctUntilChanged 
        // 
        switchMap((term: string) => this.service.getClients(term)),
      )
      .subscribe(( data  => { 
        this.allClient=data;
        this.dataSource=new MatTableDataSource<Client>(this.allClient);
        this.dataSource.paginator = this.paginator;
      }));
  }

  search(term: string): void {
    this.searchTerms.next(term);
  }
}
