
import { Bridge } from '../../class';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { BridgeService } from '../../server/bridge.service';

interface Bridge1 {
  no: string,
  name: string
}
@Component({
  selector: 'app-bridge-list',
  templateUrl: './bridge-list.component.html',
  styleUrls: ['./bridge-list.component.scss']
})
export class BridgeListComponent implements OnInit {
  
  BridgeNo: Bridge1[] = []
  selected: string=''

  allBridges:Bridge[]=[]
  dataSource:MatTableDataSource<Bridge>
  displayedColumns: string[]=['','','','','']

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  constructor(
    private bridgeService:BridgeService
  ) { }
  
  ngOnInit(): void {
    let tmp = this.bridgeService.getAllBridgeType();
    for (let i of tmp) {
      let s = i.split(":");
      let b: Bridge1 = {
        no: s[0],
        name: s[1]
      }
      this.BridgeNo.push(b)
    }
    this.bridgeService.getAllBridge().subscribe((data)=>{
      this.allBridges=data;
      this.dataSource=new MatTableDataSource<Bridge>(this.allBridges);
      this.dataSource.paginator = this.paginator;
    })
  }


}
