
import { Brand} from '../../class';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { BrandService } from 'src/app/server/brand.service';


@Component({
  selector: 'app-brand-list',
  templateUrl: './brand-list.component.html',
  styleUrls: ['./brand-list.component.scss']
})
export class BrandListComponent implements OnInit {

  allBrand:Brand[]=[]
  dataSource:MatTableDataSource<Brand>
  displayedColumns: string[]=['','']

  private searchTerms = new Subject<string>();
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  constructor(
    private service:BrandService
  ) { }
  
  ngOnInit(): void {
    this.service.getAllBrand().subscribe((data)=>{
      this.allBrand=data;
      this.dataSource=new MatTableDataSource<Brand>(this.allBrand);
      this.dataSource.paginator = this.paginator;
    })
    this.searchTerms
      .pipe(
        // debounceTime(300)  300 
        debounceTime(300),
        // 
        distinctUntilChanged(),
        //  debounce  distinctUntilChanged 
        // 
        switchMap((term: string) => this.service.getBrands(term)),
      )
      .subscribe(( data  => { 
        this.allBrand=data;
        this.dataSource=new MatTableDataSource<Brand>(this.allBrand);
        this.dataSource.paginator = this.paginator;
      }));
  }

  search(term: string): void {
    this.searchTerms.next(term);
  }
}
