

import { Component, OnInit, ViewChild } from '@angular/core';
import { BridgeService } from '../../server/bridge.service';
import { Location } from '@angular/common';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Goods } from 'src/app/class';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SharedService } from 'src/app/server/shared.service';


interface b {
  name: string
}

@Component({
  selector: 'app-good-detail',
  templateUrl: './good-detail.component.html',
  styleUrls: ['./good-detail.component.scss']
})
export class GoodDetailComponent implements OnInit {
  g:Goods;
  goodForm: FormGroup;
  brands: string[] = []
  unit: string[] = ['', '', '', '', '', '', '', '']
  constructor(
    private route: ActivatedRoute,
    private bridgeService: BridgeService,
    private location: Location,
    private _formBuilder: FormBuilder,
    private share:SharedService,
  ) { }

  ngOnInit(): void {
    const no = this.route.snapshot.paramMap.get('no');
    this.bridgeService.findGoodByNo(no).subscribe((data)=>{
      this.g=data;
    })
    this.getBrands()
    this.goodForm = this._formBuilder.group({
      no: [this.g.no],
      bname: [this.g.bname, Validators.required],
      name: [this.g.name, Validators.required],
      unit: [this.g.unit, Validators.required],
      p1: [this.g.p1, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999999)]],
      p2: [this.g.p2, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999999)]],
      p3: [this.g.p3, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999999)]],
      introduction: [this.g.introduction, Validators.required],
      stock: [this.g.stock, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999)]],
    });
  }

  getBrands() {
    this.bridgeService.getBrands().subscribe((data)=>{
      this.brands=data
    })
  }
  save() {
    alert(this.goodForm.get("dateTime").value)
  }
  submit(): void {
    this.add(this.goodForm.get('no').value, this.goodForm.get('bname').value,
      this.goodForm.get('name').value, this.goodForm.get('unit').value,
      this.goodForm.get('p1').value, this.goodForm.get('p2').value,
      this.goodForm.get('p3').value, this.goodForm.get('introduction').value,
      this.goodForm.get('stock').value)
  }
  add(no: string, bname: string, name: string, unit: string, p1: number, p2: number, p3: number, introduction: string, stock: number) {
    this.bridgeService.updateGoods({ no,bname,name,unit,p1,p2,p3,introduction,stock } as Goods).subscribe((data)=>{
      if(data==true){
        this.share.openSnackBar('','')
        this.goBack()
      }
      else{
        this.share.openSnackBar('','')
      }
        
    })
  }

  delete(no:string){
    if(confirm("?")){
      this.bridgeService.deleteGoods(no).subscribe((data)=>{
        if(data==true){
          this.share.openSnackBar('','')
          this.goBack()
        }
        else{
          this.share.openSnackBar('','')
        }
      })
    }

  }

  goBack(): void {
    this.location.back();
  }

}
