import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BridgeService } from '../../server/bridge.service';
import { Location } from '@angular/common';
interface Bridge1 {
  no: string,
  name: string
}
@Component({
  selector: 'app-bridge-add',
  templateUrl: './bridge-add.component.html',
  styleUrls: ['./bridge-add.component.scss']
})
export class BridgeAddComponent implements OnInit {
  selected: string = ''
  BridgeNo: Bridge1[] = []
  bridgeForm: FormGroup;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  constructor(
    private _formBuilder: FormBuilder,
    private bridgeService: BridgeService,
    private location: Location,
  ) { }
  ngOnInit() {
    this.getAllType();
    this.bridgeForm = this._formBuilder.group({
      bridgeName: ['', Validators.required],
      dateTime: ['', Validators.required],
      managementUnit: ['', Validators.required],
      bridgeLevel: ['', Validators.required],
      cost: ['', Validators.required],
    });
    this.firstFormGroup = this._formBuilder.group({
      bridgeTypeNo: ['', Validators.required],
    });
    this.secondFormGroup = this._formBuilder.group({
     // secondCtrl: ['', Validators.required],
      expansionJointType: [''],
      sideWalkLength: [''],
      roadWayLength: [''],
      railingLength: [''],
      pierElevation: [''],
      cappingBeamSize: [''],
      basalElevation: [''],
      floorSize: [''],
      abutmentElevation: [''],
      abutmentNumber: [''],
      expansionJointNumber: [''],
      length: [''],
      wide: [''],
    });
  }

  getAllType() {
    let tmp = this.bridgeService.getAllBridgeType();
    for (let i of tmp) {
      let s = i.split(":");
      let b: Bridge1 = {
        no: s[0],
        name: s[1]
      }
      this.BridgeNo.push(b)
    }
  }
  save(){
    alert(this.bridgeForm.get("dateTime").value)
  }
  goBack(): void {
    this.location.back();
  }
}
