import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BridgeService } from '../../server/bridge.service';
import { Location } from '@angular/common';
@Component({
  selector: 'app-sort-add',
  templateUrl: './sort-add.component.html',
  styleUrls: ['./sort-add.component.scss']
})
export class SortAddComponent implements OnInit {
  goodForm: FormGroup;
  constructor(    
    private _formBuilder: FormBuilder,
    private bridgeService: BridgeService,
    private location: Location) { }

  ngOnInit(): void {
    this.goodForm = this._formBuilder.group({
      name:['',Validators.required]
    })
  }
  submit(){
    this.bridgeService.addSort(this.goodForm.get('name').value).subscribe((data)=>{
      if(data==true){
        alert("!")
        this.goBack()
      }
      else{
        alert("")
      }
    })
  }
  goBack(): void {
    this.location.back();
  }
}
