



import { Bridge,MonitoringContent } from '../../class';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { BridgeService } from '../../server/bridge.service';
import { MatPaginator } from '@angular/material/paginator';

import { Location } from '@angular/common';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { MatSort } from '@angular/material/sort';


@Component({
  selector: 'app-bridge-detail',
  templateUrl: './bridge-detail.component.html',
  styleUrls: ['./bridge-detail.component.scss']
})
export class BridgeDetailComponent implements OnInit {

  bridge:Bridge
  monitoringContent:MonitoringContent[]=[]
  dataSource:MatTableDataSource<MonitoringContent>
  displayedColumns: string[]

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;


  constructor(
    private route: ActivatedRoute,
    private bridgeService: BridgeService,
    private location: Location,
    ) { }

  ngOnInit() {
    this.getBridge();
    // this.getMonitoringContentByNo();
  }

  getBridge(): void {
    const no = this.route.snapshot.paramMap.get('no');
    this.bridgeService.getBridgeByNo(no).subscribe((data)=>{
      this.bridge=data;
      this.getMonitoringContentByNo()
    })
   
  }

  getMonitoringContentByNo():void{
    const no = this.route.snapshot.paramMap.get('no');

    this.displayedColumns=this.bridgeService.getColOfBridgeMonitoring(this.bridge.bridgeTypeNo)
    this.bridgeService.getMonitoringContentByBridgeNo(no).subscribe((data)=>{
      this.monitoringContent=data;
      this.dataSource=new MatTableDataSource<MonitoringContent>(this.monitoringContent)
      this.dataSource.paginator=this.paginator
    })
   
  }

  goBack(): void {
    this.location.back();
  }

}

