
import { Component, OnInit, ViewChild } from '@angular/core';
import { Hero,MonitoringContent } from '../../class';
import { HeroService } from '../../hero.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { Location } from '@angular/common';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { MatSort } from '@angular/material/sort';
import { BridgeService } from '../../server/bridge.service';

@Component({
  selector: 'app-hero-list',
  templateUrl: './hero-list.component.html',
  styleUrls: ['./hero-list.component.scss']
})
export class HeroListComponent implements OnInit {
  navigationSubscription;

  dataSource:MatTableDataSource<MonitoringContent>
  displayedColumns: string[]

  @ViewChild(MatPaginator, { static: true })  // 
  paginator: MatPaginator;


  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private bridgeService:BridgeService
  ) {
    this.navigationSubscription = this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        this.initLoad();
      }
    })
  }
  initLoad() {
    let type: string = (this.route.snapshot.paramMap.get('type')) ;
    this.dataSource = new MatTableDataSource<MonitoringContent>(this.bridgeService.getMonitoringContentData(type));
    this.dataSource.paginator = this.paginator;
    this.displayedColumns=this.bridgeService.getColOfBridgeMonitoring(type)

  }
  ngOnInit() {
    this.initLoad()
  }
  ngOnDestroy() {
    if (this.navigationSubscription) {
      this.navigationSubscription.unsubscribe();
    }
  }
  setDisplayedColumns(operation:string):string[]{
    return this.bridgeService.getCol(operation)
  }



}
