import { Component, OnInit } from '@angular/core';
import { Client, Goods, Order, OrderDetail } from 'src/app/class';
import { OrderService } from 'src/app/server/order.service';
import { Location } from '@angular/common';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BridgeService } from 'src/app/server/bridge.service';
import { ClientService } from 'src/app/server/client.service';

@Component({
  selector: 'app-order-detail',
  templateUrl: './order-detail.component.html',
  styleUrls: ['./order-detail.component.scss']
})
export class OrderDetailComponent implements OnInit {

  order: Order
  orderDetail: OrderDetail[] = []
  goods: Goods[] = []
  client: Client
  orderFrom: FormGroup
  constructor(
    private orderS: OrderService,
    private goodS: BridgeService,
    private clientS: ClientService,
    private route: ActivatedRoute,
    private location: Location,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit(): void {
    this.get()
    this.orderFrom = this.formBuilder.group({
      cname: [this.client.name],
      postcode: [this.order.postcode],
      date: [this.order.date.slice(0, 19)],
      amount: [this.order.amount]
    })
  }

  get() {
    const no = this.route.snapshot.paramMap.get('no');

    this.orderS.findOrderByNo(no).subscribe((data) => {
      this.order = data;
    })
    this.orderS.getOrderDetailByNo(no).subscribe((data) => {
      this.orderDetail = data;
    })
    this.clientS.findClientByNo(this.order.cNo).subscribe((data) => {
      this.client = data;
    })
    for (let t of this.orderDetail) {
      this.goodS.findGoodByNo(t.gNo).subscribe((data) => {
        this.goods.push(data)
      })
    }
  }

  goBack(): void {
    this.location.back();
  }
}
