
import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Client } from 'src/app/class';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ClientService } from 'src/app/server/client.service';
import { SharedService } from 'src/app/server/shared.service';


@Component({
  selector: 'app-client-detail',
  templateUrl: './client-detail.component.html',
  styleUrls: ['./client-detail.component.scss']
})
export class ClientDetailComponent implements OnInit {
  c:Client;
  clientForm: FormGroup;
  hide:boolean=true
  constructor(
    private route: ActivatedRoute,
    private service: ClientService,
    private location: Location,
    private _formBuilder: FormBuilder,
    public share: SharedService
  ) { }

  ngOnInit(): void {
    const no = this.route.snapshot.paramMap.get('no');
    this.service.findClientByNo(no).subscribe((data)=>{
      this.c=data;
    })
    this.clientForm = this._formBuilder.group({
      no:[this.c.no],
      name: [this.c.name, [Validators.required,Validators.minLength(1),Validators.maxLength(10)]],
      address: [this.c.address,[Validators.required,Validators.minLength(1),Validators.maxLength(20)]],
      tNumber:[this.c.tNumber, [Validators.required,Validators.minLength(11),Validators.maxLength(11)]],
      password:[this.c.password,[Validators.required,Validators.minLength(6),Validators.maxLength(16)]]
    });
  }

  save() {
    alert(this.clientForm.get("dateTime").value)
  }
  submit(): void {
    this.add(this.clientForm.get('no').value, this.clientForm.get('name').value,this.clientForm.get('address').value,this.clientForm.get('tNumber').value)
  }
  add(no:string, name: string, address: string,tNumber:string) {
    this.service.updateClient({no, name,address,tNumber } as Client).subscribe((data)=>{
      if(data==true){
        this.share.openSnackBar('','')
        this.goBack()
      }
      else{
        this.share.openSnackBar('','')
      }
        
    })
  }

  delete(no:string){
    if(confirm("?")){
      this.service.deleteClient(no).subscribe((data)=>{
        if(data==true){
          this.share.openSnackBar('','')
          this.goBack()
        }
        else{
          this.share.openSnackBar('','')
        }
      })
    }

  }

  goBack(): void {
    this.location.back();
  }
}
