import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ClientService } from '../../server/client.service';
import { SharedService } from 'src/app/server/shared.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  loginForm: FormGroup;
  hide = true;
  constructor(
    private loginService: ClientService,
    private sharedService: SharedService,
    private router: Router,
    private formBuilder: FormBuilder,
  ) {}

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      username: [''],
      password: ['',[Validators.required,Validators.maxLength(16)]],
      // role: [ROLES.TEACHER],
    });
  }
  // 
  // data ({ data })
  login() {
    this.loginService.login(this.loginForm.get('username').value,this.loginForm.get('password').value).subscribe( ( data )  => {
      const client = data;
      if (client) {
        this.sharedService.loginedConfig(this.loginForm.value.username, client);
      }
      else{ 
        this.sharedService.openSnackBar('!','')
      }
    });
  }

  
  logout() {
    this.sharedService.logoutConfig();
  }

}
