
import { Component, OnInit,Inject } from '@angular/core';
import { Order, OrderDetail, tmpGood } from '../../class';
import { BuyService } from 'src/app/server/buy.service';
import { BridgeService } from 'src/app/server/bridge.service';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Observable, Subject,of } from 'rxjs';
import { Location } from '@angular/common';

import {MatDialog} from '@angular/material/dialog';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import {MAT_DIALOG_DATA} from '@angular/material/dialog';
import { OrderService } from 'src/app/server/order.service';
import { SharedService } from 'src/app/server/shared.service';


@Component({
  selector: 'app-car',
  templateUrl: './car.component.html',
  styleUrls: ['./car.component.scss']
})
export class CarComponent implements OnInit {
  mymap=new Map()
  tmpGoods: tmpGood[]=[];
  amount:number=0
  private searchTerms = new Subject<tmpGood>();
  constructor(
    private server: BuyService,
    private location: Location,
    private server1:BridgeService,
    public dialog: MatDialog,
    public sharedService: SharedService
    ) { }

  ngOnInit() {
    // this.server1.getAllGoods('')
    //   .subscribe((data) =>{
    //     for(let g of data){
    //       let n=0
    //       this.server.addGoods({g,n} as tmpGood).subscribe()
    //     }
    //   });
    this.server.getGoods().subscribe(data=>{
      this.tmpGoods=data;
      this.count();
    })
    this.searchTerms
    .pipe(
      // debounceTime(300)  300 
      debounceTime(300),
      // 
      distinctUntilChanged(),
      //  debounce  distinctUntilChanged 
      // 
      switchMap((term: tmpGood)=>this.check(term))
    ).subscribe(()=>{

    })



  }
  check(t: tmpGood): Observable<tmpGood> {
    if(t.n>t.g.stock || t.n<0)
      t.n=0;
    this.count()
    return of(t);
  }

  add(t:tmpGood):void{
    t.n+=1
    this.count()
  }

  sub(t:tmpGood):void{
    t.n-=1
    this.count()
  }

  count():void{
    this.amount=0;
    for(let t of this.tmpGoods){
      if(t.g.no){
        this.amount+=t.n*t.g.p2
      }    
    }
  }


  deleteGood(t:tmpGood):void {
    this.tmpGoods.splice(this.tmpGoods.indexOf(t),1)
    this.server.delete(t.id).subscribe(()=>{
      this.count()
    })
  }

  deleteAll():void{
    for(let t of this.tmpGoods){
      this.server.delete(t.id).subscribe()
    }
    this.tmpGoods.splice(0,this.tmpGoods.length)
  }

  goBack(): void {
    this.location.back();
  }

  submit():void{
    this.dialog.open(SubmitDialog,{
      disableClose: false,
      hasBackdrop: true , 
      width: '40%',
      data: { tmp: this.tmpGoods }}).afterClosed().subscribe(()=>{
      this.server.getGoods().subscribe(data=>{
        this.tmpGoods=data;
        this.count();
      })
    });
  }
}
@Component({
  selector: 'submint-dialog',
  templateUrl: 'submit-dialog.html',
  })
export class SubmitDialog {
  order:Order=new Order()
  orderDetail:OrderDetail[]=[]
  amount:number=0
  orderFrom:FormGroup
  constructor(@Inject(MAT_DIALOG_DATA) public data: {tmp: tmpGood[]},
  private server: BuyService,
  private goods:BridgeService,
  private orderServer:OrderService,
  public dialog: MatDialog,
  private _formBuilder: FormBuilder,
  public sharedService: SharedService
  ) { }

  ngOnInit(): void {
    
    for(let t of this.data.tmp){
      if(t.g.no)
        this.amount+=t.n*t.g.p2
    }
    this.order.no=this.createNo()
    this.order.cNo=this.sharedService.client.no
    this.order.postcode=this.sharedService.client.address
    this.order.amount=this.amount
    this.order.date=this.order.no.slice(2,16)
    this.orderFrom= this._formBuilder.group({
      no:[this.order.no],
      amout:[this.amount]
     })
  }



  submit():void{
    this.orderServer.addOrder(this.order).subscribe()
    for(let t of this.data.tmp){
      if(t.g.no){
        this.server.delete(t.id).subscribe()
        t.g.stock-=t.n
        let tmp:OrderDetail=new OrderDetail()
        tmp.no=this.createNo()
        tmp.oNo=this.order.no
        tmp.gNo=t.id
        tmp.n=t.n
        tmp.amount=t.n*t.g.p2
        tmp.price=t.g.p2
        this.orderServer.addOrderDetail(tmp).subscribe()
      }
    }
    this.sharedService.openSnackBar('!','')
  }


  createNo():string{
    let date = new Date();
    let n = String.fromCharCode((Math.floor(Math.random() * 26)) + 65);
    let No: string = 'D'+n + date.getFullYear();
    if ((date.getMonth()) >= 9)
        No += date.getMonth() + 1;
    else
        No += "0" + (date.getMonth() + 1);
    if (date.getDate() >= 10)
        No += date.getDate();
    else
        No += "0" + (date.getDate());
    if (date.getHours() >= 10)
        No += date.getHours();
    else
        No += "0" + (date.getHours())
    if (date.getMinutes() >= 10)
        No += date.getMinutes();
    else
        No += "0" + (date.getMinutes())
    if (date.getSeconds() >= 10)
        No += date.getSeconds();
    else
        No += "0" + date.getSeconds();
        return No
}
}
