import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroupDirective, FormGroup, NgForm, Validator, Validators } from '@angular/forms';
import { getLocaleTimeFormat, Location } from '@angular/common';
import { Order } from 'src/app/class';
import { OrderService } from 'src/app/server/order.service';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { SharedService } from 'src/app/server/shared.service';

@Component({
    selector: 'app-order-add',
    templateUrl: './order-add.component.html',
    styleUrls: ['./order-add.component.scss']
})
export class OrderAddComponent implements OnInit {

    orderForm: FormGroup;


    constructor(
        private _formBuilder: FormBuilder,
        private service: OrderService,
        private location: Location,
        private route: ActivatedRoute,
        private share:SharedService,
    ) { }
    ngOnInit() {
        const cNo = this.route.snapshot.paramMap.get('cNo');
        const amount = this.route.snapshot.paramMap.get('amount');
        const no=this.createNo();
        this.orderForm = this._formBuilder.group({
            no: [no],
            cNo:[cNo],
            postcode: ['', Validators.required],
            date: [new Date().getDate, Validators.required],
            amount: [amount, Validators.required],
        });

    }

    submit(): void {
        this.add(this.orderForm.get('no').value, 
        this.orderForm.get('cNo').value, 
        this.orderForm.get('postcode').value, 
        this.orderForm.get('date').value,
        Number.parseFloat(this.orderForm.get('amount').value))
    }
    add(no: string, cNo: string, postcode: string, date: string,amount:number) {
        this.service.addOrder({ no, cNo, postcode, date,amount } as Order).subscribe((data) => {
            if (data == true) {
                this.share.openSnackBar('','')
                this.goBack()
            }
            else {
                this.share.openSnackBar('','')
            }

        })
    }
    createNo():string{
        let date = new Date();
        let n = String.fromCharCode((Math.floor(Math.random() * 26)) + 65);
        let No: string = 'D'+n + date.getFullYear();
        if ((date.getMonth()) >= 9)
            No += date.getMonth() + 1;
        else
            No += "0" + (date.getMonth() + 1);
        if (date.getDate() >= 10)
            No += date.getDate();
        else
            No += "0" + (date.getDate());
        if (date.getHours() >= 10)
            No += date.getHours();
        else
            No += "0" + (date.getHours())
        if (date.getMinutes() >= 10)
            No += date.getMinutes();
        else
            No += "0" + (date.getMinutes())
        if (date.getSeconds() >= 10)
            No += date.getSeconds();
        else
            No += "0" + date.getSeconds();
            return No
    }



    goBack(): void {
        this.location.back();
    }

}
