import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Client } from '../class';
import * as $ from 'jquery'
@Injectable({
  providedIn: 'root'
})
export class ClientService {

  constructor() { }
  getAllClient():Observable<Client[]>{
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='getAllClient'
    var data={
      "operation":operation,
    }
    var col:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data) {
          col=of(data.results)
        }
        else {
          col=null
        }
      }
    })
     return col
  }

  getClients(key:string):Observable<Client[]>{//
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='getClients'
    var data={
      "operation":operation,
      "key":key
    }
    let col:any;
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data) {
          col=of(data.results)

        }
        else {
          col=null
        }
      }
    })
     return col
    
  }

  findClientByNo(no:string):Observable<Client>{
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='findClientByNo'
    var data={
      "operation":operation,
      "no":no
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data) {
          res=of(data.results)
        }
        else {
          res=null
        }
      }
    })
     return res
  }

  addClient(c:Client):Observable<number>{
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='addClient'
    var data={
      "operation":operation,
      "c":c
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results) {
          res=data.results
        }
        else {
          res=null
        }
      }
    })
    return of(res);
  }

  deleteClient(no:string):Observable<boolean>{
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='deleteClient'
    var data={
      "operation":operation,
      "no":no
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results==true) {
          res=of(true)
        }
        else {
          res=of(false)
        }
      }
    })
    return res;
  }

  updateClient(c:Client):Observable<boolean>{
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='updateClient'
    var data={
      "operation":operation,
      "c":c
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results==true) {
          res=of(true)
        }
        else {
          res=of(false)
        }
      }
    })
    return res;
  }

  login(name:string,password:string):Observable<Client>{
    let url="http://localhost:8080/Merchandising/ClientServlet"
    let operation:string='login'
    var data={
      "operation":operation,
      "name":name,
      "password":password
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results) {
          res=data.results
        }
        else {
          res=null
        }
      }
    })
    return of(res);
  }
}
