import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BridgeService } from '../../server/bridge.service';
import { Location } from '@angular/common';
import { Goods } from 'src/app/class';
import { SharedService } from 'src/app/server/shared.service';


interface b {
  name: string
}
@Component({
  selector: 'app-good-add',
  templateUrl: './good-add.component.html',
  styleUrls: ['./good-add.component.scss']
})
export class GoodAddComponent implements OnInit {
  goodForm: FormGroup;
  brands: string[] = []
  sorts:string[]=[]
  unit: string[] = ['', '', '', '', '', '', '', '','']


  constructor(
    private _formBuilder: FormBuilder,
    private bridgeService: BridgeService,
    private location: Location,
    private share:SharedService,
  ) { }
  ngOnInit() {
    this.getBrands()
    this.bridgeService.getSorts().subscribe(data=>{
      this.sorts=data
    })
    this.goodForm = this._formBuilder.group({
      no: ['', Validators.required],
      sort: ['',Validators.required],
      bname: ['', Validators.required],
      name: ['', Validators.required],
      unit: ['', Validators.required],
      p1: [1000, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999999)]],
      p2: [1000, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999999)]],
      p3: [1000, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999999)]],
      introduction: ['', Validators.required],
      stock: [1, [Validators.min(0), Validators.pattern(/^\d+$/), Validators.max(999)]],
    });

  }
 
  getBrands() {
    this.bridgeService.getBrands().subscribe(data=>{
      this.brands=data;
    })
    }

  getSorts(){
    
  }
  save() {
    alert(this.goodForm.get("dateTime").value)
  }
  submit(): void {
    this.add(this.goodForm.get('no').value, this.goodForm.get('bname').value,
      this.goodForm.get('name').value, this.goodForm.get('unit').value,
      this.goodForm.get('p1').value, this.goodForm.get('p2').value,
      this.goodForm.get('p3').value, this.goodForm.get('introduction').value,
      this.goodForm.get('stock').value,this.goodForm.get('sort').value)
  }
  add(no: string, bname: string, name: string, unit: string, p1: number, p2: number, p3: number, introduction: string, stock: number,sort:string) {
    this.bridgeService.addGood({ no,bname,name,unit,p1,p2,p3,introduction,stock,sort } as Goods).subscribe((data)=>{
      if(data==true){
        this.share.openSnackBar('','')
        this.goBack()
      }
      else{
        this.share.openSnackBar('','')
      }
        
    })
  }
  goBack(): void {
    this.location.back();
  }

}
