import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroupDirective, FormGroup, NgForm, Validator, Validators } from '@angular/forms';
import { getLocaleTimeFormat, Location } from '@angular/common';
import { ClientService } from 'src/app/server/client.service';
import { Client } from 'src/app/class';
import { SharedService } from 'src/app/server/shared.service';



@Component({
    selector: 'app-client-add',
    templateUrl: './client-add.component.html',
    styleUrls: ['./client-add.component.scss']
})
export class ClientAddComponent implements OnInit {

    clientForm: FormGroup;
    hide:boolean=true
    root:'root' | 'user' = 'user';
    constructor(
        private _formBuilder: FormBuilder,
        private service: ClientService,
        private location: Location,
        private share:SharedService,
        
    ) { }
    ngOnInit() {
        let date = new Date();
        let n=String.fromCharCode((Math.floor(Math.random()*26))+65);
        let No: string = n+ date.getFullYear();
        if ((date.getMonth()) >= 9)
            No += date.getMonth() + 1;
        else
            No += "0" + (date.getMonth() + 1);
        if (date.getDate() >= 10)
            No += date.getDate();
        else
            No += "0" + (date.getDate());
        if (date.getHours() >= 10)
            No += date.getHours();
        else
            No += "0" + (date.getHours())
        if (date.getMinutes() >= 10)
            No += date.getMinutes();
        else
            No += "0" + (date.getMinutes())
        if (date.getSeconds() >= 10)
            No += date.getSeconds();
        else
            No += "0" + date.getSeconds();
        this.clientForm = this._formBuilder.group({
            no: [No, Validators.required],
            name: ['', [Validators.required,Validators.minLength(1),Validators.maxLength(10)]],
            address: ['',[Validators.required,Validators.minLength(1),Validators.maxLength(20)]],
            tNumber:['', [Validators.required,Validators.minLength(11),Validators.maxLength(11)]],
            password:['',[Validators.required,Validators.minLength(6),Validators.maxLength(16)]],
        });

    }

    submit(): void {
        this.add(this.clientForm.get('no').value, this.clientForm.get('name').value,
         this.clientForm.get('address').value, this.clientForm.get('tNumber').value,
         this.clientForm.get('password').value,this.root)
    }
    add(no: string, name: string, address: string, tNumber: string,password:string,root:string) {
        this.service.addClient({ no, name, address, tNumber,password,root } as Client).subscribe((data) => {
            if (data == 2) {
                this.share.openSnackBar('','')
                this.goBack()
            }
            else if(data==1) {
                this.share.openSnackBar('!','')
            }
            else{
                this.share.openSnackBar('','')
            }
        })
    }



    goBack(): void {
        this.location.back();
    }


}
