import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Brand } from '../class';
import * as $ from 'jquery'

@Injectable({
  providedIn: 'root'
})
export class BrandService {

  constructor() { }

  getAllBrand():Observable<Brand[]>{
    let url="http://localhost:8080/Merchandising/BrandServlet"
    let operation:string='getAllBrand'
    var data={
      "operation":operation,
    }
    var col:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data) {
          col=of(data.results)
        }
        else {
          col=null
        }
      }
    })
     return col
  }

  getBrands(key:string):Observable<Brand[]>{//
    let url="http://localhost:8080/Merchandising/BrandServlet"
    let operation:string='getBrands'
    var data={
      "operation":operation,
      "key":key
    }
    let col:any;
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data) {
          col=of(data.results)

        }
        else {
          col=null
        }
      }
    })
     return col
    
  }

  findBrandByName(name:string):Observable<Brand>{
    let url="http://localhost:8080/Merchandising/BrandServlet"
    let operation:string='findBrandByName'
    var data={
      "operation":operation,
      "name":name
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data) {
          res=of(data.results)
        }
        else {
          res=null
        }
      }
    })
     return res
  }

  addBrand(b:Brand):Observable<boolean>{
    let url="http://localhost:8080/Merchandising/BrandServlet"
    let operation:string='addBrand'
    var data={
      "operation":operation,
      "b":b
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results==true) {
          res=of(true)
        }
        else {
          res=of(false)
        }
      }
    })
    return res;
  }

  deleteBrand(name:string):Observable<boolean>{
    let url="http://localhost:8080/Merchandising/BrandServlet"
    let operation:string='deleteBrand'
    var data={
      "operation":operation,
      "name":name
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results==true) {
          res=of(true)
        }
        else {
          res=of(false)
        }
      }
    })
    return res;
  }

  updateBrand(b:Brand):Observable<boolean>{
    let url="http://localhost:8080/Merchandising/BrandServlet"
    let operation:string='updateBrand'
    var data={
      "operation":operation,
      "b":b
    }
    let res:any
    $.ajax({
      async:false, 
      type: "post",
      url: url,
      dataType: "json",
      data: data,
      success: function (data) {
        if (data.results==true) {
          res=of(true)
        }
        else {
          res=of(false)
        }
      }
    })
    return res;
  }
}
