import { Injectable, NgZone } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Client } from '../class';


@Injectable({
  providedIn: 'root'
})
export class SharedService {
// HTTP, 
isLoading = false;
// 
isLogined = false;
// , 
loginUsername: string;
// 
root: string='root';
// url
redirectUrl = '/buy';
// 
errorMessage = '';

client:Client
  constructor(
    private snackBar: MatSnackBar,
    private router: Router,
    private zone: NgZone,
  ) { }
  loginedConfig(loginUsername: string, c:Client): void {
    this.isLogined = true;
    this.loginUsername = loginUsername;
    // this.autoRefreshToken();
    // const expiry = (JSON.parse(atob(token.split('.')[1]))).exp;
    // console.log(expiry);
    // console.log((Math.floor(Date.now() / 1000)));

    // You might be tempted to persist it in localstorage or cookies;
    // dont do it! This is prone to XSS or CSRF attacks.
    // https://hasura.io/blog/best-practices-of-using-jwt-with-graphql/
    // token
    // "Authorization": "Bearer eyJhbGcivzz_Mew11YNsCKxzFKqUL1EMNMiuNsX2_OdDyk-JZQ"
    this.client=c;
    this.root=c.root
    this.openSnackBar(`${loginUsername}`,'');
    // url
    this.router.navigateByUrl(this.redirectUrl);
  }
  logoutConfig(): void {
    this.openSnackBar(`${this.loginUsername}`,'');
    this.isLogined = false;
    this.loginUsername = '';
    this.root=''
    // this.redirectUrl = '/hero-list';
    // this.router.navigateByUrl(this.redirectUrl);
    this.router.navigate(['user']);
  }
  openSnackBar(info: string,action:string): void {
    this.snackBar.open(info, action,{
      duration: 1.5 * 1000,
      });
  }
}
