import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Location } from '@angular/common';
import { Brand } from 'src/app/class';
import { BrandService } from 'src/app/server/brand.service';
import { SharedService } from 'src/app/server/shared.service';



@Component({
  selector: 'app-brand-add',
  templateUrl: './brand-add.component.html',
  styleUrls: ['./brand-add.component.scss']
})
export class BrandAddComponent implements OnInit {

  brandForm: FormGroup;


  constructor(
    private _formBuilder: FormBuilder,
    private service: BrandService,
    private location: Location,
    private share:SharedService,
  ) { }
  ngOnInit() {
    this.brandForm = this._formBuilder.group({
      url: ['', Validators.required],
      name: ['',Validators.required ],
    });

  }

  submit(): void {
    this.add(this.brandForm.get('name').value, this.brandForm.get('url').value)
  }
  add(name: string, url: string) {
    this.service.addBrand({ name, url } as Brand).subscribe((data) => {
      if (data == true) {
        this.share.openSnackBar('','')
        this.goBack()
      }
      else {
        this.share.openSnackBar('','')
      }

    })
  }

  

  goBack(): void {
    this.location.back();
  }

}
