import { Component, Output, OnInit } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { SharedService } from './server/shared.service';
import { RouterOutlet, Router } from '@angular/router';
interface Bridge {
  no: string,
  name: string
}
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

export class AppComponent {
  @Output() onSend: EventEmitter<string[]> = new EventEmitter<string[]>();
  title = '';
  constructor(
    public sharedService: SharedService, private router: Router
  ) { }
  prepareRoute(outlet: RouterOutlet) {
    return outlet?.activatedRouteData && outlet.activatedRouteData['animation'];
  }
  ngOnInit() {
    
  }

  login() {
    this.router.navigate(['/user']);
  }

  logout() {
    this.sharedService.logoutConfig()
  }
}
