import { Goods, tmpGood } from '../../class';
import { Component, OnInit, Inject,ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';

import { BridgeService } from '../../server/bridge.service';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Subject } from 'rxjs';
import {MatDialog} from '@angular/material/dialog';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import {MAT_DIALOG_DATA} from '@angular/material/dialog';
import { BuyService } from 'src/app/server/buy.service';
import { SharedService } from 'src/app/server/shared.service';

@Component({
    selector: 'app-buy',
    templateUrl: './buy.component.html',
    styleUrls: ['./buy.component.scss']
})
export class BuyComponent implements OnInit {
    selected: string = ''

    allBridges: Goods[] = []
    dataSource: MatTableDataSource<Goods>
    displayedColumns: string[] = ['', '', '', '', '', '', '','']

    private searchTerms = new Subject<string>();
    @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
    constructor(
        private bridgeService: BridgeService,
        public dialog: MatDialog,
    ) { }

    ngOnInit(): void {
        this.bridgeService.getAllGoods('').subscribe((data) => {
            this.allBridges = data;
            this.dataSource = new MatTableDataSource<Goods>(this.allBridges);
            this.dataSource.paginator = this.paginator;
        })

        this.searchTerms
            .pipe(
                // debounceTime(300)  300 
                debounceTime(300),
                // 
                distinctUntilChanged(),
                //  debounce  distinctUntilChanged 
                // 
                switchMap((term: string) => this.bridgeService.getAllGoods(term)),
            )
            .subscribe((data => {
                this.allBridges = data;
                this.dataSource = new MatTableDataSource<Goods>(this.allBridges);
                this.dataSource.paginator = this.paginator;
            }));
    }

    search(term: string): void {
        this.searchTerms.next(term);
    }

    openDialog(g:Goods) {
        const dialogRef = this.dialog.open(AddDialog,{
            // disableClose: false,
            // hasBackdrop: false ,
            data: { g: g }});
        dialogRef.afterClosed().subscribe(result => {
        });
    }

}
@Component({
    selector: 'add-dialog',
    templateUrl: 'add-dialog.html',
    })
export class AddDialog {
    goodForm: FormGroup;
    constructor(@Inject(MAT_DIALOG_DATA) public data: {g: Goods},
    private _formBuilder: FormBuilder,
    private server: BuyService,
    public dialog: MatDialog,
    private share:SharedService,
    ) { }
    ngOnInit(): void {
        this.goodForm= this._formBuilder.group({
            n:[1,[Validators.required,Validators.max(this.data.g.stock),Validators.min(1)]]
        })
    }

    add(){
        this.addTmpGood(this.data.g,Number.parseInt(this.goodForm.get('n').value) )
    }
    addTmpGood(g:Goods,n:number){
        let id=g.no;
        
        this.server.addGoods({id,g,n} as tmpGood).subscribe(()=>{
            this.share.openSnackBar('','')
        })
    }
}

