import { Order} from '../../class';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { OrderService } from 'src/app/server/order.service';


@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.scss']
})
export class OrderListComponent implements OnInit {

  allOrder:Order[]=[]
  dataSource:MatTableDataSource<Order>
  displayedColumns: string[]=['','','','','']

  private searchTerms = new Subject<string>();
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  constructor(
    private service:OrderService
  ) { }
  
  ngOnInit(): void {
    this.service.getOrders("").subscribe((data)=>{
      this.allOrder=data;
      this.dataSource=new MatTableDataSource<Order>(this.allOrder);
      this.dataSource.paginator = this.paginator;
    })
    this.searchTerms
      .pipe(
        // debounceTime(300)  300 
        debounceTime(300),
        // 
        distinctUntilChanged(),
        //  debounce  distinctUntilChanged 
        // 
        switchMap((term: string) => this.service.getOrders(term)),
      )
      .subscribe(( data  => { 
        this.allOrder=data;
        this.dataSource=new MatTableDataSource<Order>(this.allOrder);
        this.dataSource.paginator = this.paginator;
      }));
  }

  search(term: string): void {
    this.searchTerms.next(term);
  }

}
