
import { Goods } from '../../class';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { BridgeService } from '../../server/bridge.service';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-good-list',
  templateUrl: './good-list.component.html',
  styleUrls: ['./good-list.component.scss']
})
export class GoodListComponent implements OnInit {

  selected: string=''

  allBridges:Goods[]=[]
  dataSource:MatTableDataSource<Goods>
  displayedColumns: string[]=['','','','','','','','','','']

  private searchTerms = new Subject<string>();
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  constructor(
    private bridgeService:BridgeService
  ) { }
  
  ngOnInit(): void {
    this.bridgeService.getAllGoods('').subscribe((data)=>{
      this.allBridges=data;
      this.dataSource=new MatTableDataSource<Goods>(this.allBridges);
      this.dataSource.paginator = this.paginator;
    })
    this.searchTerms
      .pipe(
        // debounceTime(300)  300 
        debounceTime(300),
        // 
        distinctUntilChanged(),
        //  debounce  distinctUntilChanged 
        // 
        switchMap((term: string) => this.bridgeService.getAllGoods(term)),
      )
      .subscribe(( data  => { 
        this.allBridges=data;
        this.dataSource=new MatTableDataSource<Goods>(this.allBridges);
        this.dataSource.paginator = this.paginator;
      }));
  }

  search(term: string): void {
    this.searchTerms.next(term);
  }


}
